import React, { useState } from "react"
import Layout from "../../../components/layout"

import { Carousel as AndtCarousel } from "antd"
import { Row, Col, Container, Modal, Card, Button } from "react-bootstrap"

import "./corporate.scss"
import { Link } from "gatsby"

const services = [
  {
    title: "团体保险",
    slug: "/insurance/details/group",
    content:
      "如果您缺少建房资金，得知预算后，温顶会为您挑选最合适的贷款产品。通常建筑贷款不会一次性付清，而是分期支付（通常4次）。每次发放贷款，都会重新审核贷款条件。",
    img: "",
  },
  {
    title: "企业家医疗信托",
    slug: "/insurance/details/trust",
    content: "主要针对仓库，办公室或空地等投资资金。",
    img: "",
  },
  {
    title: "企业关键人保险",
    slug: "/insurance/details/individual",
    content: "如果您是中小企业业主，需要资金运营",
    img: "",
  },
  {
    title: "高管退休计划",
    slug: "/insurance/details/retirement",
    content: "如果您是中小企业业主，需要资金运营",
    img: "",
  },
]

const Corporate = () => {
  const [showModal, setShowModal] = useState(false)
  const [title, setTitle] = useState(false)
  const [content, setContent] = useState(false)
  const handleClose = () => {
    setShowModal(false)
  }
  const handleOpen = (title, content) => {
    setShowModal(true)
    setTitle(title)
    setContent(content)
  }

  let slidesToShow = 4
  if (typeof window !== `undefined`) {
    const windowWidth = window.innerWidth
    console.log(windowWidth)
    if (windowWidth < 768) {
      slidesToShow = 1
    }
  }
  return (
    <Layout pageInfo={{ pageName: "index" }}>
      {/* Modal */}
      <div className="floater">
        <p
          style={{
            marginBottom: 0,
          }}
        >
          <b>邮件:</b> <a href="mailto:info@guaranti.ca">info@guaranti.ca</a>
          <br></br>
          <b>电话:</b> <a href="tel:+1 (778) 297 7450">+1 (778) 297 7450</a>{" "}
          <br></br>
          <div className="img-wrapper">
            <img width="120" src={require("../../../images/baoxian-caishui.jpg")} />
          </div>
        </p>
      </div>
      <Container className="mt-5 corporate-services">
        <h1
          style={{
            textAlign: "center",
          }}
        >
          温顶保险-为您保驾护航
        </h1>
        <h3>
          Shape your future web project with sharp design and refine coded
          functions.
        </h3>
        <h4 className="mt-5">企业福利保障</h4>
        <Row>
          {services.map((item, index) => {
            return (
              <Col md="6" sm="12" key={index}>
                <Card style={{ width: "100%" }}>
                  <Card.Img variant="top" src={require("../../../images/1.png")} />
                  <Card.Body>
                    <Card.Title>{item.title}</Card.Title>
                    <Link className="btn btn-primary" to={item.slug}>
                      了解更多
                    </Link>
                  </Card.Body>
                </Card>
              </Col>
            )
          })}
        </Row>

        <Row
          style={{
            marginTop: 30,
          }}
        >
          <Col md="6" sm="6" xs="6" style={{ fontSize: 32 }}>
            <Link to="/insurance/strategy">◀ 个人保险</Link>
          </Col>
          <Col
            md="6"
            sm="6"
            xs="6"
            style={{ textAlign: "right", fontSize: 32 }}
          >
            <Link to="/insurance/asset">财产保险 ▶</Link>
          </Col>
        </Row>
      </Container>
    </Layout>
  )
}

export default Corporate
